import React from "react";
import { useSkills } from "../hooks/useSkills";

const Skills = () => {
  const useSkill = useSkills;

  const handelSkills = (key: string) => {
    if (key === "libraries") {
      return (
        <div className="xl:w-60 w-full">
          {useSkill.map((skill, index) => (
            <div key={index} className="border-gray-color text-left ">
              <p className="text-xl p-1 card-border white-color">
                {skill.libraries.title}
              </p>
              {skill.libraries.skill.map((item, index) => (
                <p className="text-lg opacity-0.5 gray-color px-1" key={index}>
                  {item.library}
                </p>
              ))}
            </div>
          ))}
        </div>
      );
    }
    if (key === "languages") {
      return (
        <div className="xl:w-60 w-full">
          {useSkill.map((skill, index) => (
            <div key={index} className="border-gray-color text-left ">
              <p className="text-xl p-1 card-border white-color">
                {skill.languages.title}
              </p>
              {skill.languages.skill.map((item, index) => (
                <p className="text-lg opacity-0.5 gray-color px-1" key={index}>
                  {item.language}
                </p>
              ))}
            </div>
          ))}
        </div>
      );
    }
    if (key === "tools") {
      return (
        <div className="xl:w-60 w-full">
          {useSkill.map((skill, index) => (
            <div key={index} className="border-gray-color text-left ">
              <p className="text-xl p-1 card-border white-color">
                {skill.tools.title}
              </p>
              {skill.tools.skill.map((item, index) => (
                <p className="text-lg opacity-0.5 gray-color px-1" key={index}>
                  {item.tool}
                </p>
              ))}
            </div>
          ))}
        </div>
      );
    }
    if (key === "frameworks") {
      return (
        <div className="xl:w-60 w-full">
          {useSkill.map((skill, index) => (
            <div key={index} className="border-gray-color text-left ">
              <p className="text-xl p-1 card-border white-color">
                {skill.frameWorks.title}
              </p>
              {skill.frameWorks.skill.map((item, index) => (
                <p className="text-lg opacity-0.5 gray-color px-1" key={index}>
                  {item.framework}
                </p>
              ))}
            </div>
          ))}
        </div>
      );
    }
  };

  return (
    <div className="flex flex-col	">
      <div className="w-4/5 mx-auto text-3xl text-center text-white flex pt-10 xl:pt-20 pb-10 items-center gap-5">
        <h1 className="white-color">
          <span className="primary-color text-3xl"> #</span> skills
        </h1>
        <div className="bg-primary-color h-0.5 w-1/2  xl:w-1/5" />
      </div>
      <div className="flex flex-col xl:flex-row mx-auto justify-between w-4/5 ">
        <div className="relative h-96 w-full xl:w-1/3 ">
          <div className=" absolute top-0  right-0 border-gray-color h-32 w-36 " />
          <div className=" absolute top-7 left-20 xl:left-12 w-20 ">
            {Array(9)
              .fill(0)
              .map((_, index) => (
                <div className="dots " key={index} />
              ))}
          </div>
          <div className=" absolute top-20 left:0 xl:left-32 w-20 ">
            {Array(9)
              .fill(0)
              .map((_, index) => (
                <div className="dots " key={index} />
              ))}
          </div>
          <img
            src={require("./../../assets/logo (1).png")}
            alt="logo"
            width={"120px"}
            className=" absolute bottom-16 left-0 xl:left-16 "
          />
          <div className=" absolute bottom-32 right-0 w-20">
            {Array(9)
              .fill(0)
              .map((_, index) => (
                <div className="dots " key={index} />
              ))}
          </div>
          <div className=" absolute bottom-20 right-20 w-20">
            {Array(9)
              .fill(0)
              .map((_, index) => (
                <div className="dots " key={index} />
              ))}
          </div>
        </div>
        <div className="flex flex-col xl:flex-row   gap-5 w-full xl:w-1/2 px-5 ">
          {handelSkills("frameworks")}
          {handelSkills("languages")}
          <div className="flex flex-col gap-5">
            {handelSkills("tools")}
            {handelSkills("libraries")}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Skills;
