import { useState } from "react";
import { Link } from "react-router";

const NavBar = () => {
  const root = document.documentElement;

  const [darkMode, setDarkMode] = useState(true);

  const toggleDarkMode = ({ target }: { target: string }) => {
    if (target === "dark") {
      root.style.setProperty("--main-background", "#282c33");
      root.style.setProperty("--gray-color", "#abb2bf");
      root.style.setProperty("--white-color", "#ffffff");
      root.style.setProperty("--primary-color", "#c778dd");
      setDarkMode(true);
    }
    if (target === "light") {
      root.style.setProperty("--main-background", "#e2e8f0");
      root.style.setProperty("--gray-color", "#4f5665");
      root.style.setProperty("--white-color", "#282c33");
      root.style.setProperty("--primary-color", "#8a2be2");
      setDarkMode(false);
    }
  };

  return (
    <div className="flex justify-between px-36 pt-10 mx-auto xl:flex  hidden">
      <div className="flex item-center gap-2 ">
        <img
          src={require("./../../assets/NavBar.png")}
          alt="logo"
          width="20px"
          height="20px"
        />
        <h2 className="white-color text-xl">Abdelrhman</h2>
      </div>
      <div className="flex gap-4 ">
        <Link to={"/"} className="white-color">
          <span className="primary-color text-xl  ">#</span>
          Home
        </Link>
        <a href="#about" className="white-color">
          <span className="primary-color text-xl ">#</span>
          About
        </a>
        <a href="#skills" className="white-color">
          <span className="primary-color text-xl ">#</span>
          Skills
        </a>
        <a href="#projects" className="white-color">
          <span className="primary-color text-xl ">#</span>
          Projects
        </a>
        <a href="#contact" className="white-color">
          <span className="primary-color text-xl ">#</span>
          Contact
        </a>
        <div>
          <button
            className={
              darkMode
                ? " bg-white-color rounded-full px-1"
                : `hidden bg-white-color rounded-full px-1`
            }
            onClick={() => {
              toggleDarkMode({ target: "light" });
            }}
          >
            <i className="fa-regular fa-sun"></i>{" "}
          </button>
          <button
            className={
              darkMode
                ? "hidden bg-white-color rounded-full px-1"
                : ` bg-white-color rounded-full px-1`
            }
            onClick={() => {
              toggleDarkMode({ target: "dark" });
            }}
          >
            <i className="fa-regular fa-moon"></i>
          </button>
        </div>
      </div>
    </div>
  );
};

export default NavBar;
