import React from "react";
import { useProject } from "../hooks/useProject";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import "./styles.css";

const Works = () => {
  const useProjects = useProject;

  const [CurrentId, setCurrentId] = React.useState(0);

  return (
    <div className="mt-0 xl:mt-20 relative" id="projects">
      <div className="w-4/5 mx-auto text-3xl text-center text-white flex pt-10 xl:pt-20 pb-10 items-center gap-5">
        <h1 className="white-color text-2xl">
          <span className="primary-color text-3xl"> #</span> Projects
        </h1>
        <div className="bg-primary-color h-0.5 w-1/2  xl:w-1/5" />
      </div>
      <div className="absolute top-54 right-0 border-gray-color h-48 w-20 xl:block hidden" />
      <div className="absolute top-52 left-0 w-16 xl:block hidden">
        {Array(10)
          .fill(0)
          .map((_, index) => (
            <div className="dots " key={index} />
          ))}
      </div>
      <div className="mx-auto w-5/6 gap-5 flex ">
        <Swiper
          slidesPerView={3}
          spaceBetween={30}
          pagination={{
            clickable: true,
          }}
          className="mySwiper"
        >
          {useProjects.map((project, index) => (
            <SwiperSlide key={index}>
              <div className="flex flex-row gap-8 border-gray-color py-1">
                <div className="">
                  <img
                    src={project.img}
                    alt="img"
                    width={"100%"}
                    className="p-1 max-h-64 min-h-64"
                  />
                  <p className="gray-color text-xl card-border py-2  ">
                    {project.skills.map((skill) => (
                      <span className=" opacity-0.5 px-1" key={skill.skill}>
                        {skill.skill}
                      </span>
                    ))}
                  </p>
                  <h3 className="text-xl white-color p-5 "> {project.title}</h3>
                  <h3 className="text-xl white-color px-5 py-3 ">
                    {project.description}
                    {project.id === CurrentId && <>{project.description2}</>}
                  </h3>
                  <div className="px-5 flex flex-row gap-3 pb-3 ">
                    {project.demo && (
                      <a href={project.demo} target="blank">
                        <button className=" border-primary-color flex flex-row items-center px-4 py-1 gap-3">
                          <p className="white-color text-xl ">demo</p>
                          <i className="fa-solid fa-arrows-left-right "></i>
                        </button>
                      </a>
                    )}
                    {project.id === CurrentId ? (
                      <button
                        className=" border-gray-color flex flex-row items-center px-4 py-1 gap-3"
                        onClick={() => setCurrentId(0)}
                      >
                        <p className="gray-color text-xl">See Less</p>
                        <i className="fa-solid fa-angles-right"></i>
                      </button>
                    ) : (
                      <button
                        className=" border-gray-color flex flex-row items-center px-4 py-1 gap-3"
                        onClick={() => setCurrentId(project.id)}
                      >
                        <p className="gray-color text-xl">See More</p>
                        <i className="fa-solid fa-angles-right"></i>
                      </button>
                    )}
                  </div>
                </div>
              </div>
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    </div>
  );
};

export default Works;
