import React from "react";
import { Link } from "react-router";

const AboutMe = () => {
  return (
    <div className="flex flex-col	relative">
      <div className="w-4/5 mx-auto text-3xl text-center text-white flex pt-10 xl:pt-20 pb-10 items-center gap-5">
        <h1 className="white-color text-2xl">
          <span className="primary-color text-3xl"> #</span>About me
        </h1>
        <div className="bg-primary-color h-0.5 w-1/2  xl:w-1/5" />
      </div>
      <div className="absolute top-80  border-gray-color h-32 w-32 xl:block hidden" />
      <div className="absolute top-96 right-0 w-20 h-10 xl:block hidden">
        {Array(18)
          .fill(0)
          .map((_, index) => (
            <div className="dots " key={index} />
          ))}
      </div>
      <div className="mx-auto w-4/5 flex flex-col-reverse xl:flex-row  px-5 justify-between realtive">
        <div className=" w-full xl:w-1/2 pt-10	">
          <h1 className="white-color text-xl">Hello, im Abdelrahman</h1>
          <p className="white-color text-xl  leading-10 pt-10 pb-2">
            Junior Front-End Web Developer with 1 year of experience in React.js
            and Next.js. Proficient in using modern packages and libraries,
            including MUI, React Query, and React Redux. Highly motivated with a
            strong potential for growth, I stay focused on learning new
            technologies and staying up-to-date with the latest software
            advancements.
          </p>
          <Link to={"/ReadMore"}>
            <button className="border-primary-color px-5 py-2">
              <p className="white-color text-xl">read more</p>
            </button>
          </Link>
        </div>
        <div className="border-botton-primary ">
          <img
            src={require("../../assets/AboutImage.png")}
            alt="aboutImage"
            className=" object-cover	 "
          />
        </div>

        <div className="absolute top-48 right-96 w-32 xl:block hidden ">
          {Array(25)
            .fill(0)
            .map((_, index) => (
              <div className="dots " key={index} />
            ))}
        </div>
      </div>
    </div>
  );
};

export default AboutMe;
